import { useEffect, useState } from "react";
import "./Shipping.css";
import { useAlert } from "react-alert";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import {
  MdHome,
  MdLocationCity,
  MdOutlinePlace,
  MdPhone,
  MdPublic,
  MdTransferWithinAStation,
} from "react-icons/md";
import { Country, State } from "country-state-city";
import Button from "../../Button/Button";
import CheckOutSteps from "../CheckOutSteps/CheckOutSteps";
import { saveShippingInfo } from "../../../redux/actions/cartAction";
const Shipping = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const alert = useAlert();
  const { isAuthenticated } = useSelector((state) => state.user);
  const { shippingInfo } = useSelector((state) => state.cart);

  const [newShippingInfo, setNewShippingInfo] = useState(
    shippingInfo.address
      ? shippingInfo
      : {
          address: "",
          city: "",
          pinCode: "",
          phoneNo: "",
          country: "",
          state: "",
        }
  );

  useEffect(() => {
    if (isAuthenticated===false) {
      navigate("/login?redirect=shipping");
    }
  }, [isAuthenticated, navigate]);
  const onChangeHandler = (e) => {
    setNewShippingInfo({ ...newShippingInfo, [e.target.name]: e.target.value });
  };

  const shippingSubmit = (e) => {
    e.preventDefault();
    if(newShippingInfo.phoneNo.length < 10 ||newShippingInfo.phoneNo.length>10){
      alert.error("Phone Number should be 10 digits")
      return;
    }
    dispatch(saveShippingInfo(newShippingInfo));
    navigate("/order/confirm");
  };

  return (
    <div style={{marginTop:"3vmax"}}>
      <CheckOutSteps activeStep={0} />
      <div className="shippingContainer">
        <div className="shippingBox">
          <h2 className="shippingHeading">Shipping Details</h2>
          <form
            className="shippingForm"
            encType="multipart/form-data"
            onSubmit={shippingSubmit}
          >
            <div>
              <MdHome />
              <input
                type="text"
                placeholder="Address"
                required
                name="address"
                value={newShippingInfo.address}
                onChange={onChangeHandler}
              />
            </div>

            <div>
              <MdLocationCity />
              <input
                type="text"
                name="city"
                placeholder="City"
                required
                value={newShippingInfo.city}
                onChange={onChangeHandler}
              />
            </div>

            <div>
              <MdOutlinePlace />
              <input
                type="number"
                name="pinCode"
                placeholder="Pin Code"
                required
                value={newShippingInfo.pinCode}
                onChange={onChangeHandler}
              />
            </div>

            <div>
              <MdPhone />
              <input
                type="number"
                name="phoneNo"
                autoComplete="off"
                placeholder="Phone Number"
                required
                value={newShippingInfo.phoneNo}
                onChange={onChangeHandler}
              />
            </div>

            <div>
              <MdPublic />
              <select
                required
                value={newShippingInfo.country}
                name="country"
                onChange={onChangeHandler}
              >
                <option value="">Country</option>
                {Country.getAllCountries().map((country) => (
                  <option key={country.isoCode} value={country.isoCode}>
                    {country.name}
                  </option>
                ))}
              </select>
            </div>

            {newShippingInfo.country && (
              <div>
                <MdTransferWithinAStation />
                <select
                  required
                  name="state"
                  value={newShippingInfo.state}
                  onChange={onChangeHandler}
                >
                  <option value="">State</option>
                  {State.getStatesOfCountry(newShippingInfo.country).map(
                    (state) => (
                      <option value={state.isoCode} key={state.isoCode}>
                        {state.name}
                      </option>
                    )
                  )}
                </select>
              </div>
            )}

            <Button
              type="submit"
              disabled={newShippingInfo.state ? false : true}
              className="shippingBtn"
            >
              Continue
            </Button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Shipping;
